import React, { useState } from 'react';
import {Box, TextField, Button, Typography, Container, FormControl, InputLabel} from '@mui/material';
import {Link} from "react-router-dom";

// import Icon
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

// import Alert
import {SwalAlert, SwalAlertCallBack} from "../../components/Common/SwalAlert";
import axios from "../../api/axiosConfig";

function Login() {
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');

    const handleMenuClick = (page : string) => {
        //   
        SwalAlert('info', '', ' .');
    };

    const handleLogin = () => {

        // Validation
        if (id === '' || password === '') {
            SwalAlert('warning', ' ', '  .');
            return;
        } else if (password.length < 8) {
            SwalAlert('warning', ' ', ' 8  .');
            return;
        } else {
            axios.post('/api/auth/login', {
                id: id,
                password: password,
            }).then((response) => {
                if (response.data.success) {
                    // TODO: redux 
                    //   localStorage 
                    localStorage.setItem('user', JSON.stringify(response.data.user));

                    //   CallBack redirection 
                    SwalAlertCallBack('success', ' ', ' .', () => {
                        window.location.href = '/';
                    });
                } else {
                    SwalAlert('error', ' ', '   .');
                }
            }).catch((error) => {
                SwalAlert('error', ' ', '   .');
            });
        }
    }

    return (
        <Container maxWidth="sm">
            <Box
                sx={{
                    marginTop: 12,
                    marginBottom: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Typography component="h1" variant="h5" fontWeight={'bold'}>
                    
                </Typography>
                <Box
                     sx={{
                         mt: 5,
                         p: 7,
                         backgroundColor: 'rgb(247, 247, 247)',
                         border: '1px solid rgb(249, 249, 249)',
                         borderRadius: '10px',
                     }}
                >

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="id"
                            sx={{
                                color: 'black',
                                fontSize: '15px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="id"
                            name="id"
                            autoComplete="id"
                            autoFocus
                            value={id}
                            onChange={(e) => setId(e.target.value)}
                            placeholder={" ."}
                            InputProps={{
                                sx: {
                                    //    
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',  //   
                                    },

                                    backgroundColor: 'white',
                                    border: id === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '175px',
                                    minHeight: '40px',
                                    fontSize: '14px',
                                },
                            }}
                        />
                    </FormControl>

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="password"
                            sx={{
                                color: 'black',
                                fontSize: '15px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="password"
                            name="password"
                            autoComplete="password"
                            type="password"
                            autoFocus
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder={" ."}
                            InputProps={{
                                sx: {
                                    backgroundColor: 'white',
                                    border: password === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '175px',
                                    minHeight: '40px',
                                    fontSize: '14px',
                                },
                            }}
                        />
                    </FormControl>

                    <Box
                        sx={{
                            //  
                            display: 'flex',
                            justifyContent: 'flex-end',
                            alignItems: 'center',
                            mt: 1,
                            mb: 1,

                            gap: '7%',
                        }}
                    >
                        <Typography
                            variant="body2"
                            sx={{
                                color: 'rgba(0, 0, 0, 0.54)',
                                fontSize: '12px',
                                cursor: 'pointer',
                            }}
                            onClick = {() => handleMenuClick('findId')}
                        >
                             
                        </Typography>
                        <Typography
                            variant="body2"
                            sx={{
                                color: 'rgba(0, 0, 0, 0.54)',
                                fontSize: '12px',
                                cursor: 'pointer',
                            }}
                            onClick = {() => handleMenuClick('findPassword')}
                        >
                             
                        </Typography>
                    </Box>

                    <Button
                        fullWidth
                        variant="outlined"
                        sx={{
                            mt: 3,
                            backgroundColor: 'rgb(255, 212, 31)',
                            minHeight: '48px',
                            fontSize: '18px',
                            fontWeight: 'bold',
                            color: 'rgb(41, 41, 46)',
                            border: 'none',

                            '&:hover': {
                                backgroundColor: 'rgba(255, 212, 31, .7)',
                                border: 'none',
                            }

                            // '&:hover': {
                            //     backgroundColor: 'transparent',
                            //     border: '1px solid rgb(41, 41, 46)',
                            //     color: 'rgb(41, 41, 46)',
                            //     boxShadow: 'none',
                            // }
                        }}
                        onClick={handleLogin}
                    >
                        
                    </Button>

                    <Button
                        fullWidth
                        variant="outlined"
                        sx={{
                            mt: 1,
                            mb: 2,
                            backgroundColor: 'transparent',
                            color : 'rgb(41, 41, 46)',
                            border: '1px solid rgb(41, 41, 46)',
                            minHeight: '48px',
                            fontSize: '18px',
                            fontWeight: 'bold',

                            '&:hover': {
                                backgroundColor: 'rgba(0, 0, 0, .1)',
                                border: '1px solid rgb(41, 41, 46)',
                            }

                            // '&:hover': {
                            //     backgroundColor: 'rgb(41, 41, 46)',
                            //     color: 'white',
                            // }
                        }}
                        component={Link}
                        to={'/signup'}
                    >
                        
                    </Button>

                </Box>
            </Box>
        </Container>
    );
}

export default Login;
