import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import {SwalAlertCallBack} from "../../components/Common/SwalAlert";
import {Box, Typography} from "@mui/material";
Chart.register(...registerables);

//    
interface Visit {
    id: number;
    visit_time: string;
}

//     
interface ChartData {
    labels: string[];
    datasets: {
        label: string;
        data: number[];
        backgroundColor: string;
        borderColor: string;
        borderWidth: number;
    }[];
}

function AdminVisits() {
    const [visitData, setVisitData] = useState<ChartData>({ labels: [], datasets: [] });
    const [loading, setLoading] = useState(true);

    const fetchVisits = async () => {
        try {
            const response = await axios.get('/api/admin/visit');
            const data: ChartData = processVisitData(response.data.visits);
            setVisitData(data);
            setLoading(false);
        } catch (error) {
            console.error('    :', error);
        }
    };

    useEffect(() => {

        //   
        // TODO: redux 
        if (localStorage.getItem('user')) {
            const user = JSON.parse(localStorage.getItem('user') || '{}');
            if(user.id === 'admin' && user.name == '' && user.email == 'admin@admin') {
                fetchVisits();
            } else {
                SwalAlertCallBack('error', '  .', '  .', () => {
                    window.location.href = '/';
                });
            }
        } else {
            SwalAlertCallBack('error', '  .', '  .', () => {
                window.location.href = '/';
            });
        }
    }, []);

    //    
    const processVisitData = (visits: Visit[]): ChartData => {
        const visitCounts: Record<string, number> = {};

        visits.forEach(visit => {
            const date = new Date(visit.visit_time).toLocaleDateString();
            visitCounts[date] = (visitCounts[date] || 0) + 1;
        });

        const labels = Object.keys(visitCounts);
        const data = Object.values(visitCounts);

        return {
            labels,
            datasets: [{
                label: '  ',
                data: data,
                backgroundColor: 'rgba(0, 123, 255, 0.5)',
                borderColor: 'rgba(0, 123, 255, 1)',
                borderWidth: 1
            }]
        };
    };

    if (loading) {
        return <div> ...</div>;
    }


    return (
        <Box
            sx={{
                boxSizing: 'border-box',
                width: '80%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                margin: '0 auto',
            }}
        >
            <Typography
                variant="h4"
                component="h3"
                sx={{
                    color: 'rgb(41, 41, 46)',
                    fontSize: '28px',
                    fontWeight: 'bold',
                    mt: 3,
                    mb: 3
                }}
            >
                  -  
            </Typography>
            <Bar data={visitData} />
        </Box>
    );
}

export default AdminVisits;
