import React, { useEffect } from 'react';
import { SwalAlertCallBack } from "../../components/Common/SwalAlert";

function Logout() {
    useEffect(() => {
        //  
        SwalAlertCallBack('success', '', ' .', () => {
            //   localStorage  user 
            localStorage.removeItem('user');
            //   
            window.location.href = '/';
        });
    }, []);

    return (
        <div>

        </div>
    );
}

export default Logout;
