/**
 * file : SwalAlert.tsx
 *  : 
 *
 * Icon : success, error, warning, info, question
 *       
 */
import Swal, { SweetAlertIcon } from "sweetalert2";

/**
 *  
 * @param icon Icon 
 * @param title 
 * @param html  HTML 
 */
const SwalAlert = (icon: SweetAlertIcon, title: string, html: string | HTMLElement) => {
    Swal.fire({
        icon: icon,
        title: title,
        html: html,
        confirmButtonText: ""
    });
};

/**
 *  (  )
 * @param icon Icon 
 * @param title 
 * @param html  HTML 
 * @param CallBack  
 */
const SwalAlertCallBack = (icon: SweetAlertIcon, title: string, html: string | HTMLElement, CallBack: () => void) => {
    Swal.fire({
        icon: icon,
        title: title,
        html: html,
        confirmButtonText: ""
    }).then((res) => {
        if (res.isConfirmed) {
            CallBack();
        }
    });
};

/**
 * CallBack   (, )
 * @param icon Icon 
 * @param title 
 * @param html  HTML 
 * @param data   
 * @param CallBack  
 */
const SwalConfirm = (icon: SweetAlertIcon, title: string, html: string | HTMLElement, data: any, CallBack: (data: any) => void) => {
    Swal.fire({
        icon: icon,
        title: title,
        html: html,
        showCancelButton: true,
        confirmButtonText: "",
        cancelButtonText: "",
    }).then((res) => {
        if (res.isConfirmed) {
            CallBack(data);
        }
    });
};

export { SwalAlert, SwalAlertCallBack, SwalConfirm };
